import React, { useEffect, useState } from "react";
import { Breadcrumb, Space, List, Button } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import {
  QuestionSet,
  toHTML,
  Question,
  QUESTION,
  SYNC_QS_DATA,
  QuestionSetData,
  CLOSE,
  QS_CREATE,
  QS_EDIT,
  QUESTIONSET,
  CREATE_SUCCESS_POPUP,
  EDIT_SUCCESS_POPUP,
  DELETE_SUCCESS_POPUP,
} from "../lib";
import {
  Link,
  useSearchParams,
  useLocation,
  useNavigate,
} from "react-router-dom";
import QuestionSetEdit from "../components/QuestionSetEdit";
import { publish, subscribe, unsubscribe } from "pubsub-js";

export default function QuestionSetLib() {
  let [search] = useSearchParams();
  let id = search.get("id");
  const location = useLocation();
  const navigate = useNavigate();

  let questionSets: QuestionSet[] = JSON.parse(
    localStorage.getItem(QUESTIONSET) as string
  );
  let crtQS = id
    ? (questionSets.find((e) => {
        return e.id === id;
      }) as QuestionSet)
    : new QuestionSet("", "", []);
  let questions: Question[] = JSON.parse(
    localStorage.getItem(QUESTION) as string
  );

  const [fudata, forceUpdate] = useState<number>(0);

  const removeQuestion = (qid: string) => () => {
    let crtQuestion = questions.find((item) => item.id === qid) as Question;
    crtQuestion.questionSet = "none";
    questions[questions.findIndex((item) => item.id === qid)] = crtQuestion;
    crtQS.questions = crtQS.questions.filter((item) => item !== qid);
    questionSets[
      questionSets.findIndex((e) => {
        return e.id === crtQS.id;
      })
    ] = crtQS;
    localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    forceUpdate(Math.random());
  };

  useEffect(() => {
    id = search.get("id");
    questionSets = JSON.parse(localStorage.getItem(QUESTIONSET) as string);
    crtQS = id
      ? (questionSets.find((e) => {
          return e.id === id;
        }) as QuestionSet)
      : new QuestionSet("", "", []);
    questions = JSON.parse(localStorage.getItem(QUESTION) as string);
    publish(QUESTIONSET);
    if (search.get("edit") === "1") setIsEditModalOpen(true);
    if (search.get("del") === "1") {
      for (let index in crtQS.questions) {
        let questionIndex = questions.findIndex(
          (item) => item.id === crtQS.questions[index]
        );
        questions[questionIndex].questionSet = "none";
      }
      localStorage.setItem(QUESTION, JSON.stringify(questions));
      questionSets = questionSets.filter((item) => item.id !== crtQS.id);
      localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
      publish(DELETE_SUCCESS_POPUP);
      navigate("/quests/questionSet");
    }
  }, [location, fudata]);

  const dealWithQuestionSet = (data: QuestionSetData) => {
    const { name } = data;
    for (let index in data.questions) {
      let questionIndex = questions.findIndex(
        (item) => item.id === data.questions[index]
      );
      let questionNow = questions[questionIndex];
      if (
        questionSets.map((item) => item.name).includes(questionNow.questionSet)
        && !(questionNow.questionSet === name)
      ) {
        const qsIndex = questionSets.findIndex(
          (item) => item.name == questionNow.questionSet
        );
        questionSets[qsIndex].questions = questionSets[
          qsIndex
        ].questions.filter((item) => item !== questionNow.id);
        localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
      }
      questions[questionIndex].questionSet = name;
    }
    for (let index in questions) {
      let question = questions[index];
      if (
        question.questionSet === name &&
        !data.questions.includes(question.id)
      ) {
        question.questionSet = "none";
        questions[index] = question;
      }
    }
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    forceUpdate(Math.random());
  };

  useEffect(() => {
    subscribe(SYNC_QS_DATA, (_, data: QuestionSetData) => {
      const { name, id, desc, mode } = data;
      if (mode === QS_CREATE) {
        const QSNew = new QuestionSet(name, desc, data.questions);
        dealWithQuestionSet(data);
        questionSets = questionSets.concat([QSNew]);
        localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
        setIsCreateModalOpen(false);
        publish(CREATE_SUCCESS_POPUP);
        navigate("/quests/questionSet?id=" + QSNew.id);
      } else {
        let QSEdit = questionSets.find((item) => item.id === id) as QuestionSet;
        QSEdit.name = name;
        QSEdit.desc = desc;
        QSEdit.questions = data.questions;
        dealWithQuestionSet(data);
        questionSets[questionSets.findIndex((item) => item.id === id)] = QSEdit;
        localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
        setIsEditModalOpen(false);
        publish(EDIT_SUCCESS_POPUP);
        navigate("/quests/questionSet?id=" + id);
      }
    });
    subscribe(CLOSE, (_, mode: string) => {
      if (mode === QS_CREATE) setIsCreateModalOpen(false);
      else setIsEditModalOpen(false);
    });
    return () => {
      unsubscribe(SYNC_QS_DATA);
      unsubscribe(CLOSE);
    };
  }, []);

  const [isCreateModalOpen, setIsCreateModalOpen] = useState<boolean>(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false);

  return (
    <>
      <Breadcrumb
        items={[
          {
            href: "/home",
            title: <HomeOutlined />,
          },
          {
            href: "/quests/questionSet",
            title: "",
          },
        ]}
        style={{ marginBottom: "8px" }}
      />
      <Space>
        {!id ? (
          <Space direction="vertical">
            <Space>
              <Button onClick={() => setIsCreateModalOpen(true)}>
                
              </Button>
            </Space>
            <Space>
              <List
                size="large"
                header={<div></div>}
                bordered
                dataSource={questionSets}
                renderItem={(item) => (
                  <List.Item
                    actions={[
                      <Button
                        size="small"
                        onClick={() =>
                          navigate("/quests/questionSet?id=" + item.id + "&edit=1")
                        }
                      >
                        
                      </Button>,
                      <Button
                        size="small"
                        type="primary"
                        onClick={() =>
                          navigate("/quests/questionSet?id=" + item.id + "&del=1")
                        }
                      >
                        
                      </Button>,
                    ]}
                  >
                    <Link
                      to={"/quests/questionSet?id=" + item.id}
                      style={{ color: "black" }}
                    >
                      {item.name}
                    </Link>
                  </List.Item>
                )}
              />
            </Space>
          </Space>
        ) : (
          <Space direction="vertical" title="">
            <Space direction="vertical">
              <strong>{crtQS.name}</strong>
              {toHTML(crtQS.desc)}
            </Space>
            <List
              size="large"
              header={<div></div>}
              bordered
              dataSource={crtQS.questions}
              renderItem={(item) => (
                <List.Item
                  actions={[
                    <Button
                      size="small"
                      onClick={() =>
                        navigate("/quests/question?id=" + item + "&edit=1")
                      }
                    >
                      
                    </Button>,
                    <Button
                      size="small"
                      type="primary"
                      onClick={removeQuestion(item)}
                    >
                      
                    </Button>,
                  ]}
                >
                  <Link to={"/quests/question?id=" + item} style={{ color: "black" }}>
                    {questions.find((qs) => qs.id === item)?.name}
                  </Link>
                </List.Item>
              )}
            />
          </Space>
        )}
      </Space>
      <QuestionSetEdit
        isModalOpen={isCreateModalOpen}
        name=""
        desc=""
        id=""
        questions={[]}
        mode={QS_CREATE}
      />
      <QuestionSetEdit
        isModalOpen={isEditModalOpen}
        {...crtQS}
        mode={QS_EDIT}
      />
    </>
  );
}
