import React, { useState, useRef, useEffect } from "react";
import { publish } from "pubsub-js";
import { Modal, Space, Input, List, Button, Checkbox } from "antd";
import {
  SYNC_QS_DATA,
  QuestionSetData,
  CLOSE,
  Question,
  QS_CREATE,
  QS_EDIT,
  TAG_CREATE,
  QUESTION,
  NEW_HERE,
} from "../lib";

const { TextArea } = Input;
const { Group } = Checkbox;

interface Props {
  isModalOpen: boolean;
  name: string;
  id: string;
  desc: string;
  questions: string[];
  mode: string;
}

export default function QuestionSetEdit(props: Props) {
  const [questions, setQuestions] = useState<Question[]>(
    JSON.parse(localStorage.getItem(QUESTION) as string)
  );

  const [name, setName] = useState<string>("");
  const [desc, setDesc] = useState<string>("");
  const [questionList, setQuestionList] = useState<string[]>([]);
  const [title, setTitle] = useState<string>("");

  useEffect(() => {
    setName(props.name);
    setDesc(props.desc);
    setQuestionList(props.questions);
    switch (props.mode) {
      case QS_CREATE:
        setTitle("");
        break;
      case QS_EDIT:
        setTitle("");
        break;
      case TAG_CREATE:
        setTitle("");
        break;
      default:
        setTitle("");
    }
    setQuestions(JSON.parse(localStorage.getItem(QUESTION) as string));
  }, [props]);

  const clearContent = () => {
    setName("");
    setDesc("");
    setQuestionList([]);
    setTitle("");
  };

  const handleOk = () => {
    publish(SYNC_QS_DATA, {
      name,
      id: props.id,
      desc,
      questions: questionList,
      mode: props.mode,
    } as QuestionSetData);
    clearContent();
  };

  const handleCancel = () => {
    publish(CLOSE, props.mode);
    clearContent();
  };

  // Questions
  const [isEditState, setIsEditState] = useState<boolean>(false);

  const [questionSelected, setQuestionSelected] = useState<string[]>([]);

  const onGroupChange = (checkedValues: string[]) =>
    setQuestionSelected(checkedValues);

  return (
    <>
      <Modal
        title={title}
        open={props.isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Space direction="vertical">
          <Space direction="vertical">
            <Space direction="vertical" style={{ width: "100%" }}>
              <Space style={{ width: "100%" }}>
                
                <Input
                  placeholder=""
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  maxLength={30}
                />
              </Space>
              <Space direction="vertical" style={{ width: "100%" }}>
                
                <TextArea
                  placeholder={
                    "" +
                    (props.mode.startsWith("tag") ? "" : "") +
                    " MarkDown"
                  }
                  rows={4}
                  cols={44}
                  value={desc}
                  onChange={(e) => setDesc(e.target.value)}
                />
              </Space>
              {isEditState ? (
                <Group onChange={onGroupChange}>
                  <List
                    header={<div></div>}
                    bordered
                    dataSource={questions.concat(
                      new Question("", "", NEW_HERE, "", [], "")
                    )}
                    renderItem={(item) => {
                      if (item.name !== NEW_HERE) {
                        if (questionList.includes(item.id)) {
                          return (
                            <List.Item>
                              <Checkbox
                                value={item.id}
                                disabled={questionList.includes(item.id)}
                              >
                                {item.name}
                              </Checkbox>
                            </List.Item>
                          );
                        }
                        return (
                          <List.Item>
                            <Checkbox
                              value={item.id}
                              disabled={questionList.includes(item.id)}
                              checked={questionList.includes(item.id)}
                            >
                              {item.name}
                            </Checkbox>
                          </List.Item>
                        );
                      } else
                        return (
                          <List.Item
                            actions={[
                              <Button
                                size="small"
                                onClick={() => setIsEditState(false)}
                              >
                                
                              </Button>,
                              <Button
                                size="small"
                                type="primary"
                                onClick={() => {
                                  setQuestionList((ql) =>
                                    ql.concat(questionSelected)
                                  );
                                  setIsEditState(false);
                                }}
                              >
                                
                              </Button>,
                            ]}
                          />
                        );
                    }}
                  />
                </Group>
              ) : (
                <List
                  header={<div></div>}
                  bordered
                  dataSource={questionList.concat([NEW_HERE])}
                  renderItem={(item) => {
                    if (item !== NEW_HERE)
                      return (
                        <List.Item
                          actions={[
                            <Button
                              size="small"
                              type="primary"
                              onClick={() =>
                                setQuestionList((ql) => {
                                  ql = ql.filter((q) => q !== item);
                                  return ql;
                                })
                              }
                            >
                              
                            </Button>,
                          ]}
                        >
                          {questions.find((q) => q.id === item)?.name}
                        </List.Item>
                      );
                    else
                      return (
                        <List.Item
                          actions={[
                            <Button
                              size="small"
                              onClick={() => setIsEditState(true)}
                            >
                              
                            </Button>,
                          ]}
                          onClick={() => setIsEditState(true)}
                        >
                          
                        </List.Item>
                      );
                  }}
                />
              )}
            </Space>
          </Space>
        </Space>
      </Modal>
    </>
  );
}
