import React, { useEffect, useState } from "react";
import { Breadcrumb, Space, List, Button } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import {
  QuestionSet,
  TAG,
  toHTML,
  Question,
  QUESTION,
  SYNC_QS_DATA,
  QuestionSetData,
  CLOSE,
  TAG_CREATE,
  TAG_EDIT,
  CREATE_SUCCESS_POPUP,
  EDIT_SUCCESS_POPUP,
  DELETE_SUCCESS_POPUP,
} from "../lib";
import {
  Link,
  useSearchParams,
  useLocation,
  useNavigate,
} from "react-router-dom";
import QuestionSetEdit from "../components/QuestionSetEdit";
import { publish, subscribe, unsubscribe } from "pubsub-js";

export default function TagLib() {
  let [search] = useSearchParams();
  let id = search.get("id");
  const location = useLocation();
  const navigate = useNavigate();

  let tagList: QuestionSet[] = JSON.parse(localStorage.getItem(TAG) as string);
  let crtTag = id
    ? (tagList.find((e) => {
        return e.id === id;
      }) as QuestionSet)
    : new QuestionSet("", "", []);
  let questions: Question[] = JSON.parse(
    localStorage.getItem(QUESTION) as string
  );

  const [fudata, forceUpdate] = useState<number>(0);

  const removeQuestion = (qid: string) => () => {
    let crtQuestion = questions.find((item) => item.id === qid) as Question;
    crtQuestion.tags = crtQuestion.tags.filter((item) => item !== crtTag.name);
    questions[questions.findIndex((item) => item.id === qid)] = crtQuestion;
    crtTag.questions = crtTag.questions.filter((item) => item !== qid);
    tagList[
      tagList.findIndex((e) => {
        return e.id === crtTag.id;
      })
    ] = crtTag;
    localStorage.setItem(TAG, JSON.stringify(tagList));
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    forceUpdate(Math.random());
  };

  useEffect(() => {
    tagList = JSON.parse(localStorage.getItem(TAG) as string);
    crtTag = id
      ? (tagList.find((e) => {
          return e.id === id;
        }) as QuestionSet)
      : new QuestionSet("", "", []);
    questions = JSON.parse(localStorage.getItem(QUESTION) as string);
    publish(TAG);
    if (search.get("edit") === "1") setIsEditModalOpen(true);
    if (search.get("del") === "1") {
      for (let index in crtTag.questions) {
        let questionIndex = questions.findIndex(
          (item) => item.id === crtTag.questions[index]
        );
        questions[questionIndex].tags = questions[questionIndex].tags.filter(
          (item) => item !== crtTag.name
        );
      }
      localStorage.setItem(QUESTION, JSON.stringify(questions));
      tagList = tagList.filter((item) => item.id !== crtTag.id);
      localStorage.setItem(TAG, JSON.stringify(tagList));
      publish(DELETE_SUCCESS_POPUP);
      navigate("/quests/tag");
    }
  }, [location, fudata]);

  const dealWithTag = (data: QuestionSetData) => {
    const { name } = data;
    for (let index in data.questions) {
      let questionIndex = questions.findIndex(
        (item) => item.id === data.questions[index]
      );
      if (!questions[questionIndex].tags.includes(name))
        questions[questionIndex].tags.push(name);
    }
    for (let index in questions) {
      let question = questions[index];
      if (
        question.tags.includes(name) &&
        !data.questions.includes(question.id)
      ) {
        question.tags = question.tags.filter((item) => item !== name);
        questions[index] = question;
      }
    }
    localStorage.setItem(QUESTION, JSON.stringify(questions));
  };

  useEffect(() => {
    subscribe(SYNC_QS_DATA, (_, data: QuestionSetData) => {
      tagList = JSON.parse(localStorage.getItem(TAG) as string);
      const { name, id, desc, mode } = data;
      if (mode === TAG_CREATE) {
        const tagNew = new QuestionSet(name, desc, data.questions);
        dealWithTag(data);
        tagList = tagList.concat([tagNew]);
        localStorage.setItem(TAG, JSON.stringify(tagList));
        setIsCreateModalOpen(false);
        publish(CREATE_SUCCESS_POPUP);
        navigate("/quests/tag?id=" + tagNew.id);
      } else {
        let tagEdit = tagList.find((item) => item.id === id) as QuestionSet;
        tagEdit.name = name;
        tagEdit.desc = desc;
        tagEdit.questions = data.questions;
        dealWithTag(data);
        tagList[tagList.findIndex((item) => item.id === id)] = tagEdit;
        localStorage.setItem(TAG, JSON.stringify(tagList));
        setIsEditModalOpen(false);
        publish(EDIT_SUCCESS_POPUP);
        navigate("/quests/tag?id=" + id);
      }
    });
    subscribe(CLOSE, (_, mode: string) => {
      if (mode === TAG_CREATE) setIsCreateModalOpen(false);
      else setIsEditModalOpen(false);
    });
    return () => {
      unsubscribe(SYNC_QS_DATA);
      unsubscribe(CLOSE);
    };
  }, []);

  const [isCreateModalOpen, setIsCreateModalOpen] = useState<boolean>(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false);

  return (
    <>
      <Breadcrumb
        items={[
          {
            href: "/home",
            title: <HomeOutlined />,
          },
          {
            href: "/quests/tag",
            title: "",
          },
        ]}
        style={{ marginBottom: "8px" }}
      />
      <Space>
        {!id ? (
          <Space direction="vertical">
            <Space>
              <Button onClick={() => setIsCreateModalOpen(true)}>
                
              </Button>
            </Space>
            <Space>
              <List
                size="large"
                header={<div></div>}
                bordered
                dataSource={tagList}
                renderItem={(item) => (
                  <List.Item
                    actions={[
                      <Button
                        size="small"
                        onClick={() =>
                          navigate("/quests/tag?id=" + item.id + "&edit=1")
                        }
                      >
                        
                      </Button>,
                      <Button
                        size="small"
                        type="primary"
                        onClick={() =>
                          navigate("/quests/tag?id=" + item.id + "&del=1")
                        }
                      >
                        
                      </Button>,
                    ]}
                  >
                    <Link to={"/quests/tag?id=" + item.id} style={{ color: "black" }}>
                      {item.name}
                    </Link>
                  </List.Item>
                )}
              />
            </Space>
          </Space>
        ) : (
          <Space direction="vertical" title="">
            <Space direction="vertical">
              <strong>{crtTag.name}</strong>
              {toHTML(crtTag.desc)}
            </Space>
            <List
              size="large"
              header={<div></div>}
              bordered
              dataSource={crtTag.questions}
              renderItem={(item) => (
                <List.Item
                  actions={[
                    <Button
                      size="small"
                      onClick={() =>
                        navigate("/quests/question?id=" + item + "&edit=1")
                      }
                    >
                      
                    </Button>,
                    <Button
                      size="small"
                      type="primary"
                      onClick={removeQuestion(item)}
                    >
                      
                    </Button>,
                  ]}
                >
                  <Link to={"/quests/question?id=" + item} style={{ color: "black" }}>
                    {questions.find((qs) => qs.id === item)?.name}
                  </Link>
                </List.Item>
              )}
            />
          </Space>
        )}
      </Space>
      <QuestionSetEdit
        isModalOpen={isCreateModalOpen}
        name=""
        desc=""
        id=""
        questions={[]}
        mode={TAG_CREATE}
      />
      <QuestionSetEdit
        isModalOpen={isEditModalOpen}
        {...crtTag}
        mode={TAG_EDIT}
      />
    </>
  );
}
