import React, { useState, useRef, useEffect } from "react";
import { publish } from "pubsub-js";
import { Modal, Space, Input, Tag, Select, InputRef } from "antd";
import {
  QUESTIONSET,
  QuestionSet,
  EDIT,
  SYNC_DATA,
  QuestionData,
  CLOSE,
} from "../lib";
import { PlusCircleOutlined } from "@ant-design/icons";

const { TextArea } = Input;

interface Props {
  isModalOpen: boolean;
  stem: string;
  name: string;
  id: string;
  content: string;
  answer: string;
  tags: string[];
  questionSet: string;
  mode: string;
}

export default function QuestionEdit(props: Props) {
  let [questionSets, setQuestionSets] = useState<QuestionSet[]>(
    JSON.parse(localStorage.getItem(QUESTIONSET) as string)
  );

  const [tags, setTags] = useState<string[]>(new Array<string>());
  const [inputVisible, setInputVisible] = useState(false);
  const [inputValue, setInputValue] = useState("");
  const [editInputIndex, setEditInputIndex] = useState(-1);
  const [editInputValue, setEditInputValue] = useState("");
  const inputRef = useRef<InputRef>(null);
  const editInputRef = useRef<InputRef>(null);

  const [stem, setStem] = useState<string>("");
  const [name, setName] = useState<string>("");
  const [content, setContent] = useState<string>("");
  const [answer, setAnswer] = useState<string>("");
  const [selectNew, setSelectNew] = useState<string>("");

  useEffect(() => {
    setStem(props.stem);
    setName(props.name);
    setContent(props.content);
    setAnswer(props.answer);
    setTags(props.tags);
    setSelectKey(
      props.questionSet.length > 0
        ? props.questionSet
        : questionSets.length > 0
        ? questionSets[0].name
        : "none"
    );
    setQuestionSets(JSON.parse(localStorage.getItem(QUESTIONSET) as string));
  }, [props]);

  // Tags
  useEffect(() => {
    if (inputVisible) {
      inputRef.current?.focus();
    }
  }, [inputVisible]);

  useEffect(() => {
    editInputRef.current?.focus();
  }, [editInputValue]);

  const handleClose = (removedTag: string) =>
    setTags(tags.filter((tag) => tag !== removedTag));

  const showInput = () => {
    setInputVisible(true);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
  };

  const handleInputConfirm = () => {
    if (inputValue && !tags.includes(inputValue)) {
      setTags([...tags, inputValue]);
    }
    setInputVisible(false);
    setInputValue("");
  };

  const handleEditInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setEditInputValue(e.target.value);
  };

  const handleEditInputConfirm = () => {
    const newTags = [...tags];
    newTags[editInputIndex] = editInputValue;
    setTags(newTags);
    setEditInputIndex(-1);
    setEditInputValue("");
  };

  const tagInputStyle: React.CSSProperties = {
    width: 64,
    height: 22,
    marginInlineEnd: 8,
    verticalAlign: "top",
  };

  const tagPlusStyle: React.CSSProperties = {
    height: 22,
    borderStyle: "dashed",
  };

  const clearContent = () => {
    setStem("");
    setContent("");
    setName("");
    setAnswer("");
    setTags([]);
    setSelectNew("");
  };

  const [selectInput, setSelectInput] = useState<boolean>(false);
  const [selectKey, setSelectKey] = useState<string>(
    props.questionSet.length > 0
      ? props.questionSet
      : questionSets.length > 0
      ? questionSets[0].name
      : "none"
  );

  const handleOk = () => {
    publish(SYNC_DATA, {
      stem,
      name,
      id: props.id,
      content,
      answer,
      tags,
      questionSet: selectKey,
      mode: props.mode,
    } as QuestionData);
    clearContent();
  };

  const handleCancel = () => {
    publish(CLOSE, props.mode);
    clearContent();
  };

  let handleSelectInputConfirm = () => {
    setQuestionSets((qsList) =>
      qsList.concat([
        new QuestionSet(selectNew, "", []),
      ])
    );
    setSelectInput(false);
    setSelectNew("");
    setSelectKey(selectNew);
  };

  return (
    <>
      <Modal
        title={props.mode === EDIT ? "" : ""}
        open={props.isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Space direction="vertical">
          <Space direction="vertical">
            <Space direction="vertical" style={{ width: "100%" }}>
              
              <TextArea
                placeholder=" MarkDown"
                rows={4}
                cols={44}
                value={stem}
                onChange={(e) => setStem(e.target.value)}
              />
            </Space>
            <Space style={{ width: "100%" }}>
              
              <Input
                placeholder=""
                value={name}
                onChange={(e) => setName(e.target.value)}
                maxLength={30}
              />
            </Space>
            <Space direction="vertical" style={{ width: "100%" }}>
              
              <TextArea
                placeholder=" MarkDown"
                rows={4}
                cols={44}
                value={content}
                onChange={(e) => setContent(e.target.value)}
              />
            </Space>
            <Space direction="vertical" style={{ width: "100%" }}>
              
              <TextArea
                placeholder=" MarkDown"
                rows={4}
                cols={44}
                value={answer}
                onChange={(e) => setAnswer(e.target.value)}
              />
            </Space>
          </Space>

          <Space wrap>
            
            {tags.map((tag, index) => {
              if (editInputIndex === index) {
                return (
                  <Input
                    key={tag}
                    size="small"
                    style={tagInputStyle}
                    value={editInputValue}
                    onChange={handleEditInputChange}
                    onBlur={handleEditInputConfirm}
                    onPressEnter={handleEditInputConfirm}
                    maxLength={20}
                  />
                );
              }
              const tagElem = (
                <Tag
                  key={tag}
                  closable
                  style={{ userSelect: "none" }}
                  onClose={() => handleClose(tag)}
                >
                  <span
                    onDoubleClick={(e) => {
                      setEditInputIndex(index);
                      setEditInputValue(tag);
                      e.preventDefault();
                    }}
                  >
                    {tag}
                  </span>
                </Tag>
              );
              return tagElem;
            })}
            {inputVisible ? (
              <Input
                ref={inputRef}
                type="text"
                size="small"
                style={tagInputStyle}
                value={inputValue}
                onChange={handleInputChange}
                onBlur={handleInputConfirm}
                onPressEnter={handleInputConfirm}
                maxLength={20}
              />
            ) : (
              <Tag
                style={tagPlusStyle}
                icon={<PlusCircleOutlined />}
                onClick={showInput}
              >
                
              </Tag>
            )}
          </Space>
          <Space>
            
            <Select
              options={questionSets
                .map((qs) => ({ value: qs.name, label: <>{qs.name}</> }))
                .concat({ value: "none", label: <></> })
                .concat([
                  {
                    value: "new",
                    label: (
                      <div onClick={() => setSelectInput(true)}></div>
                    ),
                  },
                ])}
              defaultValue={selectKey}
              style={{ width: "150px" }}
              value={selectKey}
              onChange={(e) => setSelectKey(e)}
            />
          </Space>
        </Space>
      </Modal>
      <Modal
        title=""
        open={selectInput}
        onCancel={() => {
          setSelectNew("");
          setSelectInput(false);
        }}
        onOk={handleSelectInputConfirm}
      >
        <Space>
          
          <Input
            placeholder=""
            value={selectNew}
            onChange={(e) => setSelectNew(e.target.value)}
          />
        </Space>
      </Modal>
    </>
  );
}
