import React, { useState, useEffect } from "react";
import {
  TagOutlined,
  SettingOutlined,
  HomeOutlined,
  BookOutlined,
  FileTextOutlined,
  InfoCircleOutlined,
  SearchOutlined,
} from "@ant-design/icons";
import { Layout, Menu, Input, message, Select } from "antd";
import {
  QuestionSet,
  getItem,
  QUESTION,
  QUESTIONSET,
  TAG,
  EDIT_SUCCESS_POPUP,
  CREATE_SUCCESS_POPUP,
  DELETE_SUCCESS_POPUP,
} from "./lib";
import Home from "./pages/Home";
import QuestionLib from "./pages/QuestionLib";
import TagLib from "./pages/TagLib";
import QuestionSetLib from "./pages/QuestionSetLib";
import Setting from "./pages/Setting";
import About from "./pages/About";
import {
  Link,
  Navigate,
  useRoutes,
  useLocation,
  Search,
} from "react-router-dom";
import { subscribe, unsubscribe } from "pubsub-js";
import { SearchProps } from "antd/es/input";
import "./App.css"

const routes = [
  {
    path: "/quests/home",
    element: <Home />,
  },
  {
    path: "/quests/question",
    element: <QuestionLib />,
  },
  {
    path: "/quests/tag",
    element: <TagLib />,
  },
  {
    path: "/quests/questionSet",
    element: <QuestionSetLib />,
  },
  {
    path: "/quests/setting",
    element: <Setting />,
  },
  {
    path: "/quests/about",
    element: <About />,
  },
  {
    path: "/quests/",
    element: <Navigate to={"/quests/home"} />,
  },
];

const { Content, Sider } = Layout;

export default function App() {
  let [tags, setTags] = useState<QuestionSet[]>([]);
  let [questionSets, setQuestionSets] = useState<QuestionSet[]>([]);
  let [key, setKey] = useState<string>("1");
  let route = useRoutes(routes);
  const location = useLocation();

  let syncTags = () => {
    setTags(JSON.parse(localStorage.getItem(TAG) as string));
  };

  let syncQuestionSets = () => {
    setQuestionSets(JSON.parse(localStorage.getItem(QUESTIONSET) as string));
  };

  useEffect(() => {
    localStorage.setItem(TAG, JSON.stringify([]));
    localStorage.setItem(QUESTIONSET, JSON.stringify([]));
    localStorage.setItem(QUESTION, JSON.stringify([]));
    subscribe(TAG, () => {
      //  React State  LocalStorage 
      syncTags();
    });
    subscribe(QUESTIONSET, () => {
      //  React State  LocalStorage 
      syncQuestionSets();
    });
    subscribe(CREATE_SUCCESS_POPUP, () => {
      message.success("");
    });
    subscribe(EDIT_SUCCESS_POPUP, () => {
      message.success("");
    });
    subscribe(DELETE_SUCCESS_POPUP, () => {
      message.success("");
    });
    return () => {
      unsubscribe(TAG);
      unsubscribe(QUESTIONSET);
      unsubscribe(CREATE_SUCCESS_POPUP);
      unsubscribe(EDIT_SUCCESS_POPUP);
      unsubscribe(DELETE_SUCCESS_POPUP);
    };
  }, []);

  useEffect(() => {
    // 
    // 
    let search = window.location.search;
    if (search && !window.location.href.includes("question?")) {
      setKey(
        search.substring(search.indexOf("id=") + 3, search.indexOf("id=") + 35)
      );
    } else {
      switch (window.location.pathname) {
        case "/quests/home":
          setKey("1");
          break;
        case "/quests/question":
          setKey("2");
          break;
        case "/quests/tag":
          setKey("6");
          break;
        case "/quests/questionSet":
          setKey("7");
          break;
        case "/quests/setting":
          setKey("5");
          break;
        case "/quests/about":
          setKey("8");
          break;
        default:
          setKey("1");
      }
    }
  }, [location]);

  const [searchValue, setSearchValue] = useState<string>("");
  const [unvisibleTags, setUnvisibleTags] = useState<string[]>([]);
  const [unvisibleQuestionSets, setUnvisibleQuestionSets] = useState<string[]>(
    []
  );

  const handleSearch: SearchProps["onSearch"] = (_value, _e, _info) => {
    console.log("searchValue:", searchValue);
    setSearch(searchValue);
  };

  const setSearch = (value: string) => {
    setUnvisibleTags([]);
    setUnvisibleQuestionSets([]);
    for (let index in tags) {
      let tag = tags[index];
      if (!tag.name.includes(value))
        setUnvisibleTags((ulist) => ulist.concat([tag.id]));
    }
    for (let index in questionSets) {
      let qs = questionSets[index];
      if (!qs.name.includes(value))
        setUnvisibleQuestionSets((ulist) => ulist.concat([qs.id]));
    }
  };

  useEffect(() => {
    setSearch(searchValue);
  }, [searchValue]);

  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Sider theme="light">
        <Input.Search
          value={searchValue}
          onChange={(e) => setSearchValue(e.target.value)}
          placeholder=""
          style={{
            margin: "12px 0",
            marginLeft: "6px",
            display: "flex",
            alignItems: "center",
            width: "90%",
          }}
          onSearch={handleSearch}
          onBlur={() => {
            setUnvisibleTags([]);
            setUnvisibleQuestionSets([]);
          }}
          onFocus={() => setSearch(searchValue)}
        ></Input.Search>
        <Menu
          selectedKeys={[key]}
          mode="inline"
          defaultOpenKeys={["3", "4"]}
          items={[
            getItem(<Link to={"/quests/home"}></Link>, "1", <HomeOutlined />),
            getItem(
              <Link to={"/quests/question"}></Link>,
              "2",
              <FileTextOutlined />
            ),
            getItem(
              "",
              "3",
              <TagOutlined />,
              [getItem(<Link to={"/quests/tag"}></Link>, "6")].concat(
                tags
                  .filter((item) => !unvisibleTags.includes(item.id))
                  .map((tag) =>
                    getItem(
                      <Link to={"/quests/tag?id=" + tag.id}>{tag.name}</Link>,
                      tag.id
                    )
                  )
              )
            ),
            getItem(
              "",
              "4",
              <BookOutlined />,
              [getItem(<Link to={"/quests/questionSet"}></Link>, "7")].concat(
                questionSets
                  .filter((item) => !unvisibleQuestionSets.includes(item.id))
                  .map((qs) =>
                    getItem(
                      <Link to={"/quests/questionSet?id=" + qs.id}>{qs.name}</Link>,
                      qs.id
                    )
                  )
              )
            ),
            getItem(
              <Link to={"/quests/setting"}></Link>,
              "5",
              <SettingOutlined />
            ),
            getItem(
              <Link to={"/quests/about"}></Link>,
              "8",
              <InfoCircleOutlined />
            ),
          ]}
        />
      </Sider>
      <Layout>
        <Content style={{ margin: "0 16px" }}>
          <div
            style={{
              padding: 24,
              minHeight: 360,
            }}
          >
            {route}
          </div>
        </Content>
      </Layout>
    </Layout>
  );
}
