import React from "react";
import MarkdownIt from "markdown-it";
import { MenuProps } from "antd";
import { md5 } from "js-md5";
const md = MarkdownIt();

export type MenuItem = Required<MenuProps>["items"][number];

export const QUESTION = "question";
export const TAG = "tag";
export const QUESTIONSET = "questionSet";

export const SYNC_DATA = "syncData";
export const SYNC_QS_DATA = "syncQuestionSetData";

export const CREATE_SUCCESS_POPUP = "createSuccessPopup";
export const EDIT_SUCCESS_POPUP = "editSuccessPopup";
export const DELETE_SUCCESS_POPUP = "deleteSuccessPopup";

// modes
export const EDIT = "edit";
export const CREATE = "create";
export const TAG_EDIT = "tagEdit";
export const QS_EDIT = "questionSetEdit";
export const TAG_CREATE = "tagCreate";
export const QS_CREATE = "questionSetCreate";
export const CLOSE = "close";

export const NEW_HERE = "newHERE!Q!+3.1.4.1.45615";

export interface QuestionData {
  stem: string;
  name: string;
  id: string;
  content: string;
  answer: string;
  tags: string[];
  questionSet: string;
  mode: string;
}

export interface QuestionSetData {
  name: string;
  id: string;
  desc: string;
  mode: string;
  questions: string[];
}

export let getItem = (
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[]
): MenuItem => {
  return {
    key,
    icon,
    children,
    label,
  } as MenuItem;
};

export function toHTML(str: string) {
  return (
    <div
      dangerouslySetInnerHTML={{
        __html: md.render(str).replaceAll("\n", "<br />"),
      }}
    />
  );
}

export class Question {
  id: string;
  name: string;
  stem: string;
  content: string;
  answer: string;
  tags: string[];
  questionSet: string;

  constructor(
    stem: string,
    content: string,
    name: string,
    answer: string,
    tags: string[],
    questionSet: string
  ) {
    this.stem = stem;
    this.content = content;
    this.name = name;
    this.id = md5(this.name + Math.random());
    this.answer = answer;
    this.tags = tags;
    this.questionSet = questionSet;
  }
}

export class QuestionSet {
  id: string;
  name: string;
  desc: string;
  questions: string[];

  constructor(name: string, desc: string, questions: string[]) {
    this.name = name;
    this.desc = desc;
    this.questions = questions;
    this.id = md5(this.name + this.desc + this.questions.toString());
  }
}
