import React from "react";
import { Breadcrumb, Space, Row, Col, Card } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";

export default function Home() {
  return (
    <>
      <Space direction="vertical" style={{ display: "flex" }}>
        <Breadcrumb
          items={[
            {
              href: "/home",
              title: <HomeOutlined />,
            },
            {
              href: "/home",
              title: "",
            },
          ]}
          style={{ marginBottom: "8px" }}
        />
        <div style={{ margin: "8px 0" }}> Quests</div>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="" extra={<Link to="/quests/question"></Link>}>
              <p></p>
            </Card>
          </Col>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="" extra={<Link to="/quests/tag"></Link>}>
              <p></p>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="" extra={<Link to="/quests/setting"></Link>}>
              <p>
                 JSON
                 JSON  Quests
              </p>
            </Card>
          </Col>
          <Col span={11} style={{ margin: "8px" }}>
            <Card
              title=""
              extra={<Link to="/quests/questionSet"></Link>}
            >
              <p></p>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col span={11} style={{ margin: "8px" }}>
            <Card title="" extra={<Link to="/quests/about"></Link>}>
              <p> Quests </p>
            </Card>
          </Col>
        </Row>
      </Space>
    </>
  );
}
