import React, { useState } from "react";
import { Button, Space, Breadcrumb, message, Input } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import InfoCard from "../components/InfoCard";
import { Base64 } from "js-base64";
import { Question, QuestionSet, QUESTION, TAG, QUESTIONSET } from "../lib";
import { publish } from "pubsub-js";

const { encode, decode } = Base64;
const { TextArea } = Input;

export default function Setting() {
  let questions: Question[] = JSON.parse(
    localStorage.getItem(QUESTION) as string
  );
  let tagList: QuestionSet[] = JSON.parse(localStorage.getItem(TAG) as string);
  let questionSets: QuestionSet[] = JSON.parse(
    localStorage.getItem(QUESTIONSET) as string
  );

  let encoded = "";
  let [undecoded, setUndecoded] = useState<string>("");

  const [canCopy, setCanCopy] = useState<boolean>(true);
  const copyToClickboard = () => {
    encoded =
      encode(localStorage.getItem(QUESTION) as string) +
      ":" +
      encode(localStorage.getItem(TAG) as string) +
      ":" +
      encode(localStorage.getItem(QUESTIONSET) as string);
    navigator.clipboard.writeText(encoded).then(
      () => {
        message.success("");
        setCanCopy(true);
      },
      () => {
        message.error("");
        setCanCopy(false);
      }
    );
  };

  const syncOverride = () => {
    if (undecoded.split(":").length !== 3) {
      message.error("");
      return;
    }
    [questions, tagList, questionSets] = undecoded
      .split(":")
      .map((item) => JSON.parse(decode(item)));

    localStorage.setItem(QUESTION, JSON.stringify(questions));
    localStorage.setItem(TAG, JSON.stringify(tagList));
    localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
    publish(QUESTIONSET);
    publish(TAG);
    setUndecoded("");
    message.success("");
  };

  const syncAppend = () => {
    if (undecoded.split(":").length !== 3) {
      message.error("");
      return;
    }
    const decodeList = undecoded
      .split(":")
      .map((item) => JSON.parse(decode(item)));
    questions = questions.concat(decodeList[0]);
    tagList = tagList.concat(decodeList[1]);
    questionSets = questionSets.concat(decodeList[2]);
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    localStorage.setItem(TAG, JSON.stringify(tagList));
    localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
    publish(QUESTIONSET);
    publish(TAG);
    setUndecoded("");
    message.success("");
  };

  const emptyAll = () => {
    questions = [];
    questionSets = [];
    tagList = [];
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    localStorage.setItem(TAG, JSON.stringify(tagList));
    localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
    message.success("");
    publish(QUESTIONSET);
    publish(TAG);
  };

  const emptyAllWithCopy = () => {
    copyToClickboard();
    emptyAll();
  };

  return (
    <>
      <Breadcrumb
        items={[
          {
            href: "/home",
            title: <HomeOutlined />,
          },
          {
            href: "/quests/setting",
            title: "",
          },
        ]}
        style={{ marginBottom: "8px" }}
      />
      <Space direction="vertical">
        <h3></h3>
        <InfoCard>
          <strong></strong>
          <Space>
            <Button onClick={emptyAll}></Button>
            <Button onClick={emptyAllWithCopy}></Button>
          </Space>
        </InfoCard>
        <h3></h3>
        <InfoCard>
          <strong></strong>
          <Button onClick={copyToClickboard}></Button>
        </InfoCard>
        {canCopy ? <></> : <InfoCard>{encoded}</InfoCard>}
        <InfoCard>
          <strong> Quests</strong>
          <TextArea
            placeholder=""
            rows={6}
            value={undecoded}
            onChange={(e) => setUndecoded(e.target.value)}
            cols={44}
          />
          <Space>
            <Button onClick={syncOverride}></Button>
            <Button onClick={syncAppend}></Button>
          </Space>
        </InfoCard>
      </Space>
    </>
  );
}
