import React, { useState, useEffect } from "react";
import { Breadcrumb, Space, Button, Tag, List } from "antd";
import { HomeOutlined } from "@ant-design/icons";
import {
  Link,
  useNavigate,
  useSearchParams,
  useLocation,
} from "react-router-dom";
import {
  Question,
  QUESTION,
  QUESTIONSET,
  QuestionSet,
  TAG,
  toHTML,
  SYNC_DATA,
  QuestionData,
  CREATE,
  CLOSE,
  EDIT,
  DELETE_SUCCESS_POPUP,
  EDIT_SUCCESS_POPUP,
  CREATE_SUCCESS_POPUP,
} from "../lib";
import { subscribe, unsubscribe, publish } from "pubsub-js";
import QuestionEdit from "../components/QuestionEdit";

export default function QuestionLib() {
  let questions: Question[] = JSON.parse(
    localStorage.getItem(QUESTION) as string
  );
  let tagList: QuestionSet[] = JSON.parse(localStorage.getItem(TAG) as string);
  let [questionSets, setQuestionSets] = useState<QuestionSet[]>(
    JSON.parse(localStorage.getItem(QUESTIONSET) as string)
  );

  let [search] = useSearchParams();
  let navigate = useNavigate();
  let id = search.get("id");
  let del = search.get("del");
  let location = useLocation();

  let crtQuestion: Question = id
    ? (questions.find((item) => item.id === id) as Question)
    : new Question("", "", "", "", [], "none");

  let [isCreateModalOpen, setIsCreateModalOpen] = useState<boolean>(false);
  let [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(
    search.get("edit") === "1"
  );

  //  Question 
  function dealWithQuestion(
    question: Question,
    tags: string[],
    id: string,
    questionSet: string
  ) {
    //  Tag
    for (let index in question.tags) {
      const tag = tags[index];
      if (tagList.map((item) => item.name).includes(tag)) {
        if (tagList.find((item) => item.name === tag)?.questions.includes(id))
          continue;
        tagList.find((item) => item.name === tag)?.questions.push(question.id);
      } else
        tagList.push(
          new QuestionSet(tag, "", [question.id])
        );
    }
    //  Tag
    for (let index in tagList) {
      let tag = tagList[index];
      if (tag.questions.includes(id) && !tags.includes(tag.name)) {
        tag.questions = tag.questions.filter((item) => item !== id);
        tagList[index] = tag;
      }
    }
    // 
    if (questionSets.map((item) => item.name).includes(questionSet)) {
      if (
        !questionSets
          .find((item) => item.name === questionSet)
          ?.questions.includes(id)
      )
        questionSets
          .find((item) => item.name === questionSet)
          ?.questions.push(question.id);
    } else if (questionSet !== "none") {
      questionSets.push(
        new QuestionSet(questionSet, "", [
          question.id,
        ])
      );
    }
    for (let index in questionSets) {
      let qs = questionSets[index];
      if (qs.questions.includes(id) && questionSet !== qs.name) {
        qs.questions = qs.questions.filter((item) => item !== id);
        questionSets[index] = qs;
      }
    }
    //  localStorage
    localStorage.setItem(QUESTION, JSON.stringify(questions));
    localStorage.setItem(TAG, JSON.stringify(tagList));
    localStorage.setItem(QUESTIONSET, JSON.stringify(questionSets));
    //  App
    publish(TAG);
    publish(QUESTIONSET);
  }

  useEffect(() => {
    subscribe(CLOSE, (_, data) => {
      if (data === EDIT) setIsEditModalOpen(false);
      else setIsCreateModalOpen(false);
    });
    subscribe(SYNC_DATA, (_, data: QuestionData) => {
      const { stem, content, name, answer, tags, questionSet, mode, id } = data;
      questions = JSON.parse(localStorage.getItem(QUESTION) as string);
      tagList = JSON.parse(localStorage.getItem(TAG) as string);
      setQuestionSets(JSON.parse(localStorage.getItem(QUESTIONSET) as string));
      if (mode === CREATE) {
        const question = new Question(
          stem,
          content,
          name,
          answer,
          tags,
          questionSet
        );
        questions.push(question);
        dealWithQuestion(question, tags, id, questionSet);
        setIsCreateModalOpen(false);
        publish(CREATE_SUCCESS_POPUP);
      } else {
        const index = questions.findIndex((item) => item.id === id);
        let question = new Question(
          stem,
          content,
          name,
          answer,
          tags,
          questionSet
        );
        question.id = id;
        questions[index] = question;
        dealWithQuestion(question, tags, id, questionSet);
        setIsEditModalOpen(false);
        navigate("/quests/question?id=" + id, { replace: true });
        publish(EDIT_SUCCESS_POPUP);
      }
    });
    return () => {
      unsubscribe(CLOSE);
      unsubscribe(SYNC_DATA);
    };
  }, []);

  useEffect(() => {
    setIsEditModalOpen(search.get("edit") === "1");
  }, [search]);

  useEffect(() => {
    crtQuestion = id
      ? (questions.find((item) => item.id === id) as Question)
      : new Question("", "", "", "", [], "none");
    if (del === "1") {
      crtQuestion.tags = [];
      crtQuestion.questionSet = "none";
      dealWithQuestion(
        crtQuestion,
        crtQuestion.tags,
        crtQuestion.id,
        crtQuestion.questionSet
      );
      localStorage.setItem(
        QUESTION,
        JSON.stringify(questions.filter((item) => item.id !== crtQuestion.id))
      );
      navigate("/quests/question", { replace: true });
      publish(DELETE_SUCCESS_POPUP);
      id = null;
      del = null;
    }
    questions = JSON.parse(localStorage.getItem(QUESTION) as string);
    tagList = JSON.parse(localStorage.getItem(TAG) as string);
    setQuestionSets(JSON.parse(localStorage.getItem(QUESTIONSET) as string));
  }, [location]);

  return (
    <>
      <Space direction="vertical" style={{ display: "flex" }}>
        <Breadcrumb
          items={[
            {
              href: "/home",
              title: <HomeOutlined />,
            },
            {
              href: "/quests/question",
              title: "",
            },
          ]}
          style={{ marginBottom: "8px" }}
        />
        {!id || del ? (
          <>
            <Space>
              <Button onClick={() => setIsCreateModalOpen(true)}>
                
              </Button>
            </Space>
            <Space>
              <List
                size="large"
                header={<div></div>}
                bordered
                dataSource={questions}
                renderItem={(item) => (
                  <List.Item
                    actions={[
                      <Button
                        size="small"
                        onClick={() =>
                          navigate("/quests/question?id=" + item.id + "&edit=1")
                        }
                      >
                        
                      </Button>,
                      <Button
                        size="small"
                        type="primary"
                        onClick={() =>
                          navigate("/quests/question?id=" + item.id + "&del=1")
                        }
                      >
                        
                      </Button>,
                    ]}
                  >
                    <Link
                      to={"/quests/question?id=" + item.id}
                      style={{ color: "black" }}
                    >
                      {item.name}
                    </Link>
                  </List.Item>
                )}
              />
            </Space>
          </>
        ) : (
          <Space.Compact direction="vertical" title="" >
            <Space direction="vertical">
              <Space>
                {crtQuestion.tags.map((tag) => {
                  return (
                    <Tag key={tag}>
                      <Link
                        to={
                          "/quests/tag?id=" +
                          tagList.find((item) => item.name === tag)?.id
                        }
                        style={{ color: "black" }}
                      >
                        {tag}
                      </Link>
                    </Tag>
                  );
                })}
              </Space>
              <Space
                style={{
                  width: "100%",
                  backgroundColor: "white",
                  borderRadius: "10px",
                  margin: "16px 0",
                  padding: "0 16px",
                }}
              >
                {toHTML(crtQuestion.stem)}
              </Space>
              <strong>{toHTML(crtQuestion.name)}</strong>
              {toHTML(crtQuestion.content)}
              <strong></strong>
              {toHTML(crtQuestion.answer)}
              <strong>
                
                {crtQuestion.questionSet === "none" ? (
                  ""
                ) : (
                  <Link
                    to={
                      "/quests/questionSet?id=" +
                      questionSets.find(
                        (item) => item.name === crtQuestion.questionSet
                      )?.id
                    }
                    style={{ color: "gray" }}
                  >
                    {crtQuestion.questionSet}
                  </Link>
                )}
              </strong>
            </Space>
          </Space.Compact>
        )}
        <QuestionEdit
          {...crtQuestion}
          isModalOpen={isCreateModalOpen}
          mode={CREATE}
        />
        <QuestionEdit
          {...crtQuestion}
          isModalOpen={isEditModalOpen}
          mode={EDIT}
        />
      </Space>
    </>
  );
}
