import React, { ReactNode } from "react";
import { Space } from "antd";

export default function InfoCard(props: { children?: ReactNode }) {
  return (
    <Space
      style={{
        width: "100%",
        backgroundColor: "white",
        borderRadius: "6px",
        margin: "8px",
        padding: "16px",
      }}
      direction="vertical"
    >
      {props.children}
    </Space>
  );
}
